function NumberFormatCustom3(props) {
  const { inputRef, data, onChange, ...other } = props;
  const MAX_VAL = 2147483648;
  const MIN_VAL = -2147483648;
  const withValueLimit = ({ value }) => {
    console.log("chalo3TA", props?.value, props?.data?.value, value, data)
    data.value = value 
    return (value >= MIN_VAL && value <= MAX_VAL || value === "" || value=== "-")
}
  var noPrefix;
  var  noSuffix;
  return (
    <NumberFormat
      {...other}
      onValueChange={({ value: v }) => onChange({ target: {value: v } })}
      customInput={TextareaAutosize}
      allowNegative
      isNumericString
      isAllowed={withValueLimit}
        decimalScale={0}
    />
  );
};

function NumberFormatCustom2(props) {
  const { inputRef, data, onChange, ...other } = props;
  const MAX_VAL = 9223372036854775808;
  const MIN_VAL = -9223372036854775808;
  const withValueLimit = ({ value }) => {
    console.log("chalo2TA", props?.value, props?.data?.value, value, data)
    data.value = value 
    return (value >= MIN_VAL && value <= MAX_VAL || value === "" || value=== "-")
}
  
  var noPrefix;
  var  noSuffix;
  return (
    <NumberFormat
      {...other}
      onValueChange={({ value: v }) => onChange({ target: {value: v } })}
      customInput={TextareaAutosize}
      allowNegative
      isNumericString
      isAllowed={withValueLimit}
        decimalScale={0}

    />
  );
};

function NumberFormatCustom1(props) {
  const { inputRef, data, onChange, ...other } = props;
  const withValueLimit = ({ value }) => {
    console.log("chalo1TA", props?.value, props?.data?.value, value, data)
    data.value = value 
    return true
}
  
  var noPrefix;
  var  noSuffix;

  return (
    <NumberFormat
      {...other}
      onValueChange={({ value: v }) => onChange({ target: {value: v } })}
      allowNegative
      customInput={TextareaAutosize}
      isNumericString
      isAllowed={withValueLimit}
      decimalScale = {data.datamapVariableType === "Float" && data?.datamapPercision !== undefined && data?.datamapPercision !== 0 ? parseInt(data.datamapPercision) : window?.allowPrecisionInText()}

    />
  );
};

